/**
 * @author coin_details
 */

"use dom";

import { useEffect, useRef } from "react";

import type {
  ChartingLibraryFeatureset,
  ChartingLibraryWidgetOptions,
  IChartingLibraryWidget,
  ResolutionString
} from "../../../../../assets/tradingview/charting_library";
import { widget } from "../../../../../assets/tradingview/charting_library";

import type { AdvancedChartBar } from "@/hooks/charts/advanced/useAdvancedChartData";
import { createAdvancedChartDatafeed } from "@/services/charting/AdvancedChartDatafeed";

type ChartTypeOption = "area" | "candles";

interface AdvancedChartDOMProps {
  mint: string;
  width: number;
  height: number;
  backgroundColor: string;
  textColor: string;
  upColor: string;
  downColor: string;
  initialData: AdvancedChartBar[];
  resolution: ResolutionString;
  timeframeWindowSeconds?: number;
  chartType?: ChartTypeOption;
  dom?: {
    containerStyle?: React.CSSProperties;
  };
}

interface TradingViewTimeScaleApi {
  fitContent?: () => void;
  setRightOffset?: (offset: number) => void;
}

const AdvancedChartDOM = ({
  mint,
  width,
  height,
  backgroundColor,
  textColor,
  upColor,
  downColor,
  initialData,
  resolution,
  timeframeWindowSeconds,
  chartType = "area"
}: AdvancedChartDOMProps) => {
  const containerRef = useRef<HTMLDivElement>(null);
  const chartInstanceRef = useRef<IChartingLibraryWidget | null>(null);
  const isChartReadyRef = useRef(false);
  const chartTypeRef = useRef<ChartTypeOption>(chartType);
  const subscriberCallbackRef = useRef<
    ((bar: AdvancedChartBar) => void) | null
  >(null);

  const dataRef = useRef<AdvancedChartBar[]>(initialData);

  useEffect(() => {
    dataRef.current = initialData;
  }, [initialData]);

  useEffect(() => {
    if (
      !subscriberCallbackRef.current ||
      !isChartReadyRef.current ||
      initialData.length === 0
    ) {
      return;
    }

    const lastBar = initialData.at(-1);
    if (!lastBar) return;

    const resolutionMatch = resolution.match(/^(\d+)([DHMS]?)$/);
    let bucketSeconds = 60;

    if (resolutionMatch) {
      const value = parseInt(resolutionMatch[1], 10);
      const unit = resolutionMatch[2] || "M";

      switch (unit) {
        case "S":
          bucketSeconds = value;
          break;
        case "M":
        case "":
          bucketSeconds = value * 60;
          break;
        case "H":
          bucketSeconds = value * 3600;
          break;
        case "D":
          bucketSeconds = value * 86_400;
          break;
      }
    }

    const barTimeSeconds = Math.floor(lastBar.time / 1000);
    const bucketedTime =
      Math.floor(barTimeSeconds / bucketSeconds) * bucketSeconds;

    subscriberCallbackRef.current({
      time: bucketedTime * 1000,
      open: lastBar.open,
      high: lastBar.high,
      low: lastBar.low,
      close: lastBar.close,
      volume: lastBar.volume
    });

    if (
      timeframeWindowSeconds &&
      chartInstanceRef.current &&
      isChartReadyRef.current
    ) {
      try {
        const chart = chartInstanceRef.current.activeChart();
        const toSeconds = bucketedTime;
        const fromSeconds = toSeconds - timeframeWindowSeconds;

        void chart.setVisibleRange({ from: fromSeconds, to: toSeconds });
      } catch (_error) {
        // Ignore
      }
    }
  }, [initialData, timeframeWindowSeconds, resolution]);

  const applyChartType = (type: ChartTypeOption) => {
    const chart = chartInstanceRef.current?.activeChart();
    if (!chart) {
      return;
    }

    const styleId = type === "area" ? 3 : 1;

    const typedChart = chart as unknown as {
      setChartType?: (style: number) => void;
    };

    if (typeof typedChart.setChartType === "function") {
      typedChart.setChartType(styleId);
    }
  };

  useEffect(() => {
    chartTypeRef.current = chartType;
    if (isChartReadyRef.current) {
      applyChartType(chartType);
    }
  }, [chartType]);

  useEffect(() => {
    if (!containerRef.current) return;

    if (chartInstanceRef.current) {
      chartInstanceRef.current.remove();
      chartInstanceRef.current = null;
    }

    const containerId = `tv_chart_${mint}`;
    containerRef.current.id = containerId;

    const datafeed = createAdvancedChartDatafeed({
      mint,
      getDataRef: () => dataRef.current,
      onSubscribe: (callback) => {
        subscriberCallbackRef.current = callback;
      },
      onUnsubscribe: () => {
        subscriberCallbackRef.current = null;
      }
    });

    const disabledFeatures: ChartingLibraryFeatureset[] = [
      "use_localstorage_for_settings",
      "save_chart_properties_to_local_storage",
      "header_widget",
      "header_symbol_search",
      "symbol_search_hot_key",
      "header_compare",
      "header_indicators",
      "header_quick_search",
      "header_screenshot",
      "header_fullscreen_button",
      "header_chart_type",
      "header_resolutions",
      "header_settings",
      "header_saveload",
      "header_undo_redo",
      "timeframes_toolbar",
      "left_toolbar",
      "control_bar",
      "legend_widget",
      "symbol_info",
      "study_templates",
      "datasource_copypaste",
      "border_around_the_chart",
      "countdown",
      "main_series_scale_menu",
      "widget_logo" as ChartingLibraryFeatureset,
      "move_time_scale" as ChartingLibraryFeatureset,
      "pinch_scale" as ChartingLibraryFeatureset,
      "touch_scale" as ChartingLibraryFeatureset,
      "scales_date_format" as ChartingLibraryFeatureset,
      "scales_time_hours_format" as ChartingLibraryFeatureset
    ];

    const enabledFeatures: ChartingLibraryFeatureset[] = ["seconds_resolution"];

    const widgetOptions: ChartingLibraryWidgetOptions = {
      symbol: `Coin ${mint.slice(0, 8)}`,
      interval: resolution,
      container: containerId,
      datafeed,
      library_path: "/assets/tradingview/charting_library/",
      custom_css_url: "/assets/tradingview/custom-theme.css",
      locale: "en",
      disabled_features: disabledFeatures,
      enabled_features: enabledFeatures,
      theme: "dark",
      autosize: true,
      load_last_chart: false,
      auto_save_delay: 0,
      toolbar_bg: backgroundColor,
      loading_screen: {
        backgroundColor,
        foregroundColor: upColor
      },
      overrides: {
        "paneProperties.background": backgroundColor,
        "paneProperties.backgroundType": "solid",
        "scalesProperties.backgroundColor": backgroundColor,
        "scalesProperties.textColor": textColor,
        "scalesProperties.lineColor": backgroundColor,
        "scalesProperties.fontSize": 9,
        "scalesProperties.fontFamily":
          "Inter, system-ui, -apple-system, BlinkMacSystemFont, sans-serif",
        "paneProperties.vertGridProperties.color": "transparent",
        "paneProperties.horzGridProperties.color": "transparent",
        "mainSeriesProperties.style": 3, // Area chart
        "mainSeriesProperties.areaStyle.color1": upColor,
        "mainSeriesProperties.areaStyle.color2": backgroundColor,
        "mainSeriesProperties.areaStyle.linecolor": upColor,
        "mainSeriesProperties.areaStyle.linewidth": 2,
        "mainSeriesProperties.areaStyle.transparency": 90,
        "mainSeriesProperties.candleStyle.upColor": upColor,
        "mainSeriesProperties.candleStyle.downColor": downColor,
        "mainSeriesProperties.candleStyle.borderUpColor": upColor,
        "mainSeriesProperties.candleStyle.borderDownColor": downColor,
        "mainSeriesProperties.candleStyle.wickUpColor": upColor,
        "mainSeriesProperties.candleStyle.wickDownColor": downColor,
        "timeScale.lockVisibleTimeRangeOnResize": true,
        "timeScale.rightOffset": 0,
        "scalesProperties.rightMargin": 0
      },
      custom_formatters: {
        priceFormatterFactory: () => ({
          format: (value: number) => {
            // Format market cap as compact USD (e.g., "585K", "1.2M")
            if (value >= 1_000_000_000) {
              return `$${(value / 1_000_000_000).toFixed(1)}B`;
            }
            if (value >= 1_000_000) {
              return `$${(value / 1_000_000).toFixed(1)}M`;
            }
            if (value >= 1000) {
              return `$${(value / 1000).toFixed(1)}K`;
            }
            return `$${value.toFixed(0)}`;
          }
        })
      }
    };

    const tvWidget = new widget(widgetOptions);
    chartInstanceRef.current = tvWidget;

    tvWidget.onChartReady(() => {
      isChartReadyRef.current = true;
      applyChartType(chartTypeRef.current);

      if (chartInstanceRef.current) {
        const chart = chartInstanceRef.current.activeChart();
        const data = dataRef.current;
        if (data && data.length > 0) {
          if (timeframeWindowSeconds) {
            const lastBar = data[data.length - 1];
            const windowStartMs = lastBar.time - timeframeWindowSeconds * 1000;
            const fromSeconds = Math.floor(windowStartMs / 1000);
            const toSeconds = Math.floor(lastBar.time / 1000);

            void chart.setVisibleRange({
              from: fromSeconds,
              to: toSeconds
            });
          } else {
            const timeScale =
              chart.getTimeScale() as unknown as TradingViewTimeScaleApi;
            timeScale.fitContent?.();
          }
        }
      }
    });

    return () => {
      isChartReadyRef.current = false;
      if (chartInstanceRef.current) {
        chartInstanceRef.current.remove();
        chartInstanceRef.current = null;
      }
    };
  }, [
    mint,
    resolution,
    backgroundColor,
    textColor,
    upColor,
    downColor,
    timeframeWindowSeconds
  ]);

  return (
    <div
      style={{
        position: "relative",
        width: `${width}px`,
        height: `${height}px`
      }}
    >
      <div
        ref={containerRef}
        style={{
          width: "100%",
          height: "100%",
          backgroundColor,
          pointerEvents: "none"
        }}
      />
    </div>
  );
};

export default AdvancedChartDOM;
