import type { ChartResolution } from "@/utils/chart";

/**
 * Converts mobile ChartResolution format to TradingView ResolutionString format
 *
 * Mobile uses descriptive strings like "1m", "5m", "1h"
 * TradingView uses numeric strings like "1", "5", "60"
 *
 * @param resolution - Mobile chart resolution (e.g., "1m", "5m", "1h")
 * @returns TradingView resolution string (e.g., "1", "5", "60")
 * @example
 * toTradingViewResolution("1m") // Returns "1"
 * toTradingViewResolution("1h") // Returns "60"
 * toTradingViewResolution("24h") // Returns "1D"
 */
export const toTradingViewResolution = (
  resolution: ChartResolution
): string => {
  const mapping: Record<ChartResolution, string> = {
    "1s": "1S",
    "15s": "15S",
    "30s": "30S",
    "1m": "1",
    "5m": "5",
    "15m": "15",
    "30m": "30",
    "1h": "60",
    "4h": "240",
    "6h": "360",
    "12h": "720",
    "24h": "1D"
  };

  return mapping[resolution] || "1";
};

/**
 * Returns all supported TradingView resolutions
 * These correspond to the mobile ChartResolution values
 */
export const getSupportedTradingViewResolutions = (): string[] => {
  return [
    "1S",
    "15S",
    "30S",
    "1",
    "5",
    "15",
    "30",
    "60",
    "240",
    "360",
    "720",
    "1D"
  ];
};
